@use "../abstracts" as *;

/*! common */
// .clfix{*zoom:1}
.clfix:after,
.clfix:before {
  display: table;
  line-height: 0;
  content: "";
}
.clfix:after {
  clear: both;
}

//-     .   .
//-            .
.hide-txt {
  // display:inline-block;
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  font-size: 1px;
  line-height: 0;
  // clip:rect(1px 1px 1px 1px);
  clip: rect(0 0 0 0);
  white-space: nowrap;
  margin: -1px;
  padding: 0;
  border: 0;
}

// a  hide-txt     font-size:1px  font-size:0       .
//      max-width    .
@media (max-width: 1024px) {
  a .hide-txt {
    font-size: 0;
  }
}

//   
// a  before    
a {
  &:before {
    content: "";
    position: absolute;
    font-size: 0;
  }
}

//       
[tabindex="-1"] {
  &:focus {
    outline: 0;
  }
}
// [tabindex="-1"]:focus:not(:focus-visible) {
//   outline: 0 !important;
// }

/*!        */
.mobile-mode-dimm-container {
  display: none;
}
@media (max-width: 1023px) {
  .mobile-mode-dimm-container {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    outline: 0;
    overflow: hidden;
    text-align: center;
    z-index: $mobile-mode-dimm-container;
    background: #000;
    opacity: 1;
    .mobile-mode-box {
      position: relative;
      @include rem(padding, 20px);
      @include rem(margin, 0 40px);
      background: #fff;
      @include rem(border-radius, 10px);
      border: 2px solid blue;
      @include rem(font-size, 24px);
    }
  }
}

/*! skipnav */
.skipnav {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 0;
  line-height: 1;
}
.skipnav a {
  display: block;
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: 0 -1px -1px 0;
  padding: 0;
  font-size: 0;
  line-height: 1;
}
.skipnav a:active,
.skipnav a:focus,
.skipnav a:hover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  width: 100%;
  height: 19px;
  margin: 0;
  padding: 5px 0;
  border: 1px solid #c4d7ea;
  background: #2b6cb0;
  color: #fff;
  font-weight: 700;
  font-size: 1.333em;
  text-align: center;
  line-height: 100%;
}

/*! Browser Upgrade Prompt */
.browserupgrade {
  margin: 0.2em 0;
  padding: 0.2em 0;
  background: #ccc;
  color: #000;
  font-size: 1px;
}
.browserupgrade strong {
  color: #000;
  font-weight: 700;
}
.browserupgrade a {
  color: #8d0080;
  font-weight: 700;
  text-decoration: underline;
}

/*! dimm */
.dimm {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background: #000;
  opacity: 0.6;
}
.dimm img {
  display: block;
}

/*!    */
html,
body {
  height: 100%;
}
#wrap {
  height: 100%;
}
#container {
  height: 100%;
}
#content {
  height: 100%;
}

/*!    */
// mobile, pc 
[class^="ico-"] {
  display: inline-block;
  vertical-align: middle;
  background-repeat: no-repeat;
  background-position: center center;
}
