$global-background-color: #ffffff;
$global-font-color: #000000;
$global-font-size: 10px !default;
$global-font-size02: 10;

// ******************** code comments ********************
//   <html lang="">
// font-familu, line-height, word-break, word-wrap, letter-spacing
// ******************** code comments ********************
$global-font-family: (
  // 'ko': ('Malgun Gothic', ' ', Dotum, '', arial, verdana, sans-serif),
  // 'en': (Helvetica, Arial, sans-serif),
  // "ko":
  //   (
  //     Pretendard,
  //     -apple-system,
  //     BlinkMacSystemFont,
  //     system-ui,
  //     Roboto,
  //     "Helvetica Neue",
  //     "Segoe UI",
  //     "Apple SD Gothic Neo",
  //     "Noto Sans KR",
  //     "Malgun Gothic",
  //     "Apple Color Emoji",
  //     "Segoe UI Emoji",
  //     "Segoe UI Symbol",
  //     sans-serif
  //   ),
  "ko":
    (
      "Pretendard",
      -apple-system,
      BlinkMacSystemFont,
      "Noto Sans KR",
      "Malgun Gothic",
      " ",
      Dotum,
      "",
      sans-serif
    ),
  "en": (
    "Pretendard Variable",
    Pretendard,
    -apple-system,
    BlinkMacSystemFont,
    system-ui,
    Roboto,
    "Helvetica Neue",
    "Segoe UI",
    "Apple SD Gothic Neo",
    "Noto Sans KR",
    "Malgun Gothic",
    "Apple Color Emoji",
    "Segoe UI Emoji",
    "Segoe UI Symbol",
    sans-serif
  ),
  "ja": (
    "Meiryo",
    "",
    " Pro W3",
    "Hiragino Kaku Gothic Pro",
    sans-serif
  ),
  "zh": (
    Helvetica,
    Arial,
    "Microsoft Yahei",
    "",
    STXihei,
    "",
    sans-serif
  )
);
$global-line-height: (
  "ko": (
    1.15,
  ),
  "en": (
    1.15,
  ),
  "ja": (
    1.15,
  ),
  "zh": (
    1.15,
  ),
);
$global-word-break: (
  "ko": (
    keep-all,
  ),
  "en": (
    break-all,
  ),
  "ja": (
    break-all,
  ),
  "zh": (
    break-all,
  ),
);
$global-word-wrap: (
  "ko": (
    break-word,
  ),
  "en": (
    break-word,
  ),
  "ja": (
    break-word,
  ),
  "zh": (
    break-word,
  ),
);
$global-letter-spacing: (
  "ko": (
    0,
  ),
  "en": (
    -0.02em,
  ),
  "ja": (
    -0.02em,
  ),
  "zh": (
    -0.02em,
  ),
);

// ******************** code comments ********************
// z-index   
//  z-index  +, -   
//     
//    z-index     
// , z-index:1, z-index:2  10   css  
// ******************** code comments ********************
$base-z-index: 100; //  z-index
$mobile-mode-dimm-container: $base-z-index + 900; //       z-index 1000
$popup-zindex: $base-z-index; //  z-index 100
$global-dimm-zindex: $base-z-index - 10; //   z-index 90
$gnb-container-zindex: $base-z-index - 15; // pc & mobile gnb z-index 85
$mobile-subpage-header-title-zindex: $base-z-index - 19; //     fixed z-index 81
$header-scroll-fixed-zindex: $base-z-index - 20; //   fixed z-index 80

